import React, { useState } from 'react';
import { REAL_MISSION_LOGBOOK } from '../../data/real_missions';
import { TrainingStats, ReplayStep } from '../../types';

type AISidebarProps = {
    currentStepText: string;
    logs: string[];
    isThinking: boolean;
    isTraining: boolean;
    trainingStats: TrainingStats | null;
    replayData: ReplayStep[] | null;
    isReplayMode: boolean;
    onNextStep: () => void;
    onLoadMission: (id: number) => void;
    onReset: () => void;
    onStartTraining: () => void;
    onLoadReplay: () => void;
    onNextReplayStep: () => void;
    onExitReplay: () => void;
};

export default function AISidebar({ 
    currentStepText, logs, isThinking, isTraining, trainingStats, replayData, isReplayMode,
    onNextStep, onLoadMission, onReset, onStartTraining, onLoadReplay, onNextReplayStep, onExitReplay
}: AISidebarProps) {
    const [selectedMission, setSelectedMission] = useState(1);

    return (
        <div className="w-96 bg-[#0f172a] border-r border-gray-800 flex flex-col h-full shadow-2xl z-20 font-sans text-gray-300">
            {/* ENTRAINEMENT */}
            <div className="p-5 border-b border-gray-800 bg-[#1e293b]/50">
                <h1 className="text-xs font-black text-white mb-3 uppercase tracking-wider flex items-center gap-2">
                    <span className="text-purple-500">üß†</span> R√©seau de Neurones
                </h1>
                
                {!isTraining && !trainingStats && !isReplayMode && (
                    <button onClick={onStartTraining} className="w-full py-2 bg-purple-600 hover:bg-purple-500 text-white text-xs font-bold rounded shadow transition-all">
                        D√âMARRER L'APPRENTISSAGE
                    </button>
                )}

                {(isTraining || trainingStats) && !isReplayMode && (
                    <div className="space-y-3">
                        {/* PROGRESSION DE LA SESSION */}
                        <div className="flex justify-between text-[10px] font-mono text-gray-400">
                            <span>SESSION: {trainingStats?.episode} / {trainingStats?.max_episodes}</span>
                            <span className="text-blue-400">TOTAL: {trainingStats?.total_lifetime_games}</span>
                        </div>
                        <div className="w-full h-1.5 bg-gray-800 rounded-full overflow-hidden">
                            <div className="h-full bg-purple-500 transition-all duration-300" style={{ width: `${((trainingStats?.episode||0)/(trainingStats?.max_episodes||1))*100}%` }}></div>
                        </div>

                        {/* STATS DE PERFORMANCE */}
                        <div className="grid grid-cols-2 gap-2 mt-2">
                            <div className="bg-black/20 p-2 rounded border border-white/5 flex flex-col items-center">
                                <span className="text-[9px] text-gray-500 uppercase">Score Moyen (Force)</span>
                                <span className={`text-sm font-bold ${(trainingStats?.avg_reward||0)>0?'text-green-400':'text-red-400'}`}>
                                    {trainingStats?.avg_reward}
                                </span>
                            </div>
                            <div className="bg-black/20 p-2 rounded border border-white/5 flex flex-col items-center">
                                <span className="text-[9px] text-gray-500 uppercase">Exploration</span>
                                <span className="text-sm font-bold text-blue-400">
                                    {((trainingStats?.epsilon || 0) * 100).toFixed(1)}%
                                </span>
                            </div>
                        </div>
                        
                        {/* CONTROLE REPLAY */}
                        {replayData && (
                            <div className="pt-2 border-t border-gray-700/50 mt-2">
                                <button onClick={onLoadReplay} className="w-full py-1.5 bg-gray-700 hover:bg-gray-600 text-[10px] font-bold text-white rounded flex items-center justify-center gap-2">
                                    üì∫ VOIR LE MEILLEUR REPLAY
                                </button>
                            </div>
                        )}
                    </div>
                )}

                {/* MODE REPLAY ACTIF */}
                {isReplayMode && (
                    <div className="bg-blue-900/20 border border-blue-500/30 p-3 rounded">
                        <div className="flex justify-between items-center mb-2">
                            <span className="text-[10px] font-bold text-blue-300 animate-pulse">MODE REPLAY</span>
                            <button onClick={onExitReplay} className="text-red-400 hover:text-white text-[10px]">QUITTER</button>
                        </div>
                        <button onClick={onNextReplayStep} className="w-full py-2 bg-blue-600 hover:bg-blue-500 text-white text-xs font-bold rounded">
                            COUP SUIVANT ‚ñ∂
                        </button>
                    </div>
                )}
            </div>

            {/* MISSION CONFIG (Reste inchang√©) */}
            <div className={`p-4 border-b border-gray-800 bg-[#1e293b] ${isTraining||isReplayMode ? 'opacity-30 pointer-events-none' : ''}`}>
                <div className="flex gap-2 mb-2">
                    <select className="flex-1 bg-gray-900 text-white text-[10px] p-2 rounded border border-gray-700" value={selectedMission} onChange={(e) => setSelectedMission(Number(e.target.value))}>
                        {REAL_MISSION_LOGBOOK.map(m => <option key={m.id} value={m.id}>MISSION {m.id}</option>)}
                    </select>
                    <button onClick={() => onLoadMission(selectedMission)} className="bg-blue-600 hover:bg-blue-500 text-white px-3 rounded text-[10px] font-bold">CHARGER</button>
                </div>
                <button onClick={onReset} className="text-red-400 text-[10px] hover:text-red-300 block w-full text-right">Reset Table</button>
            </div>

            {/* ACTIONS (Reste inchang√©) */}
            <div className="p-4 flex flex-col gap-3 border-b border-gray-800 bg-gradient-to-b from-[#0f172a] to-[#1e293b]">
                <div className="text-sm font-bold text-white min-h-[1.5rem] leading-tight">{currentStepText}</div>
                <button onClick={onNextStep} disabled={isThinking || isTraining || isReplayMode} className="w-full py-3 bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-500 hover:to-indigo-500 text-white rounded font-bold shadow-lg disabled:opacity-50 text-xs">
                    {isThinking ? '...' : '‚ñ∂ DEMANDER A L\'IA'}
                </button>
            </div>

            {/* LOGS */}
            <div className="flex-1 overflow-y-auto p-3 space-y-1 font-mono text-[10px] text-gray-500">
                {logs.map((log, i) => <div key={i} className="border-l-2 border-gray-800 pl-2">{log}</div>)}
            </div>
        </div>
    );
}