import React from 'react';
import { CardData, MissionUI } from '../../types';
import Card from '../Card';

const MissionToken = ({ mission }: { mission: MissionUI }) => (
    <div className="relative w-10 h-14 bg-gray-800 rounded border border-gray-600 shadow-xl flex flex-col items-center justify-center transform hover:scale-110 transition-transform z-20">
        {mission.token !== 'None' && (
            <div className="absolute -top-2 -right-2 w-5 h-5 bg-purple-600 rounded-full flex items-center justify-center text-[10px] text-white font-bold shadow border border-white/20">
                {mission.token === 'Omega' ? 'Œ©' : mission.token}
            </div>
        )}
        <div className={`w-full h-1.5 mb-1 ${
            mission.cardColor === 'Pink' ? 'bg-pink-500' : 
            mission.cardColor === 'Green' ? 'bg-green-500' : 
            mission.cardColor === 'Blue' ? 'bg-blue-500' : 
            mission.cardColor === 'Yellow' ? 'bg-yellow-500' : 'bg-gray-600'
        }`}></div>
        <div className="text-white font-bold text-sm">{mission.cardValue}</div>
    </div>
);

type GodGameBoardProps = {
    allCards?: CardData[];
    missions?: MissionUI[];
    onPlayerClick?: (playerIndex: number) => void;
};

export default function GodGameBoard({ 
    allCards = [], missions = [], onPlayerClick 
}: GodGameBoardProps) {
  
  const safeCards = Array.isArray(allCards) ? allCards : [];
  const safeMissions = Array.isArray(missions) ? missions : [];
  const DIST_NORD = 260; const DIST_SUD  = 260; const DIST_OUEST= 380; const DIST_EST  = 380;
  const LABEL_OFFSET = 100; const HAND_RADIUS = 600; const ANGLE_STEP = 5;

  const getCardStyle = (card: CardData): React.CSSProperties => {
      
      // 1. ANIMATION DE VICTOIRE
      if (card.status === 'WON') {
          const winPositions = [`translate(0px, 600px)`, `translate(-800px, 0px)`, `translate(0px, -600px)`, `translate(800px, 0px)`];
          return { 
              transform: `translate(-50%, -50%) ${winPositions[card.winnerIndex || 0]} scale(0.5)`, 
              opacity: 0, 
              zIndex: 500, 
              // Transition sp√©cifique, pas de z-index
              transition: 'transform 0.8s ease-in-out, opacity 0.8s ease-in-out'
          };
      }

      // 2. CARTE SUR TABLE
      if (card.status === 'TABLE') {
          const rX = (card.tableRotation * 5) % 15 - 7;
          const rY = (card.tableRotation * 7) % 15 - 7;
          return { 
              transform: `translate(-50%, -50%) translate(${rX}px, ${rY}px) rotate(${card.tableRotation}deg) scale(1.1)`, 
              opacity: 1, 
              zIndex: 200 + card.zIndex, 
              // --- CORRECTIONS ICI ---
              // 1. ease-in-out standard
              // 2. Transition explicite pour ne PAS animer le z-index
              transition: 'transform 0.5s ease-out, opacity 0.5s ease-out'
          };
      }
      
      // 3. CARTE EN MAIN
      const cardsInHand = safeCards.filter(c => c.owner === card.owner && c.status === 'HAND');
      const colorPriority: Record<string, number> = { 'Blue': 1, 'Green': 2, 'Pink': 3, 'Yellow': 4, 'Rocket': 5 };
      cardsInHand.sort((a, b) => { if (a.color !== b.color) return colorPriority[a.color] - colorPriority[b.color]; return a.value - b.value; });
      const myIndex = cardsInHand.findIndex(c => c.id === card.id);
      const total = cardsInHand.length;
      const angle = (myIndex - (total - 1) / 2) * ANGLE_STEP;
      const centerHTML = "translate(-50%, -50%)";
      let playerRotation = 0; let distance = 0;
      switch(card.owner) {
        case 0: playerRotation = 0; distance = DIST_SUD; break;
        case 1: playerRotation = 90; distance = DIST_OUEST; break;
        case 2: playerRotation = 180; distance = DIST_NORD; break;
        case 3: playerRotation = -90; distance = DIST_EST; break;
      }
      const transform = `${centerHTML} rotate(${playerRotation}deg) translate(0px, ${distance + HAND_RADIUS}px) rotate(${angle}deg) translate(0px, -${HAND_RADIUS}px)`;
      
      return { 
          transform, 
          opacity: 1, 
          zIndex: myIndex,
          transition: 'transform 0.5s ease-out' // Idem ici
      };
  };

  const getMissionContainerStyle = (playerIndex: number): React.CSSProperties => {
      const base = "translate(-50%, -50%)";
      switch(playerIndex) {
          case 0: return { transform: `${base} translate(0px, ${DIST_SUD - LABEL_OFFSET}px)` };
          case 1: return { transform: `${base} translate(-${DIST_OUEST - LABEL_OFFSET}px, 0px) rotate(90deg)` };
          case 2: return { transform: `${base} translate(0px, -${DIST_NORD - LABEL_OFFSET}px)` };
          case 3: return { transform: `${base} translate(${DIST_EST - LABEL_OFFSET}px, 0px) rotate(-90deg)` };
          default: return {};
      }
  };

  const getTitleStyle = (playerIndex: number): React.CSSProperties => {
      switch(playerIndex) {
          case 0: return { bottom: '180px', left: '50%', transform: 'translateX(-50%)' };
          case 1: return { left: '240px', top: '50%', transform: 'translateY(-50%) rotate(90deg)' };
          case 2: return { top: '180px', left: '50%', transform: 'translateX(-50%)' };
          case 3: return { right: '240px', top: '50%', transform: 'translateY(-50%) rotate(-90deg)' };
          default: return {};
      }
  };

  const playerNames = ['SUD', 'OUEST', 'NORD', 'EST'];

  return (
    <div className="w-full h-full relative bg-[#2c3e50] overflow-hidden shadow-inner flex items-center justify-center">
        {onPlayerClick && [0, 1, 2, 3].map(pid => (
            <div 
                key={`title-${pid}`}
                className="absolute z-30 cursor-pointer font-black text-white/30 hover:text-white text-lg uppercase tracking-[0.2em] transition-colors select-none"
                style={getTitleStyle(pid) as any}
                onClick={() => onPlayerClick(pid)}
            >
                {playerNames[pid]}
            </div>
        ))}
        <div className="relative w-0 h-0 flex items-center justify-center">
            {[0, 1, 2, 3].map(pid => {
                const pMissions = safeMissions.filter(m => m.ownerIndex === pid);
                if (pMissions.length === 0) return null;
                return (
                    <div key={pid} className="absolute flex gap-2 pointer-events-none" style={getMissionContainerStyle(pid)}>
                        {pMissions.map((m, i) => <MissionToken key={i} mission={m} />)}
                    </div>
                );
            })}
            {safeCards.map((card) => {
                const style = getCardStyle(card);
                return (
                    <div 
                        key={card.id}
                        className="absolute top-0 left-0 w-20 aspect-[2/3] pointer-events-none"
                        // On s'assure que seule la transformation et l'opacit√© sont optimis√©es pour l'animation
                        style={{ ...style, willChange: 'transform, opacity' }}
                    >
                        <Card color={card.color} value={card.value} className="w-full h-full shadow-2xl border border-black/30 rounded-lg" />
                    </div>
                );
            })}
        </div>
      </div>
  );
}