import React from 'react';
import { CardData, MissionUI } from '../../types';
import Card from '../Card';

const MissionToken = ({ mission }: { mission: MissionUI }) => {
    const statusBorder = mission.status === 'SUCCESS' ? 'border-green-500 shadow-[0_0_10px_rgba(34,197,94,0.6)]' :
        mission.status === 'FAILED' ? 'border-red-500 opacity-60' :
        'border-gray-500';

    const statusIcon = mission.status === 'SUCCESS' ? <div className="absolute -bottom-2 -right-2 bg-green-500 rounded-full w-5 h-5 flex items-center justify-center text-xs shadow">‚úÖ</div> :
        mission.status === 'FAILED' ? <div className="absolute -bottom-2 -right-2 bg-red-500 rounded-full w-5 h-5 flex items-center justify-center text-xs shadow">‚ùå</div> :
        null;

    return (
        <div className={`relative w-10 h-14 bg-gray-900 rounded border-2 ${statusBorder} flex flex-col items-center justify-center transform transition-all z-20`}>
            {mission.token !== 'None' && (
                <div className="absolute -top-2 -right-2 w-5 h-5 bg-purple-600 rounded-full flex items-center justify-center text-[10px] text-white font-bold shadow border border-white/20 z-10">
                    {mission.token === 'Omega' ? 'Œ©' : mission.token}
                </div>
            )}
            <div className={`w-full h-1.5 mb-1 ${
                mission.cardColor === 'Pink' ? 'bg-pink-500' : 
                mission.cardColor === 'Green' ? 'bg-green-500' : 
                mission.cardColor === 'Blue' ? 'bg-blue-500' : 
                mission.cardColor === 'Yellow' ? 'bg-yellow-500' : 'bg-gray-600'
            }`}></div>
            <div className="text-white font-bold text-sm">{mission.cardValue}</div>
            {statusIcon}
        </div>
    );
};

type AIGameBoardProps = {
    allCards?: CardData[];
    missions?: MissionUI[];
    probabilities?: Record<string, number>;
    suggestedCardId?: string | null;
    activePlayer?: number;
    onCardClick?: (id: string) => void;
};

export default function AIGameBoard({ 
    allCards = [], 
    missions = [], 
    probabilities = {}, 
    suggestedCardId, 
    // onCardClick n'est plus utilis√© ici car on d√©sactive le clic
    activePlayer = 0
}: AIGameBoardProps) {

  // On garde la logique visuelle pour griser les cartes injouables (feedback visuel seulement)
  const getPlayableStatus = (card: CardData): boolean => {
      if (card.owner !== activePlayer) return false;
      const trickCards = allCards.filter(c => c.status === 'TABLE');
      if (trickCards.length === 0) return true;
      trickCards.sort((a, b) => a.zIndex - b.zIndex);
      const leadColor = trickCards[0].color;
      const myHand = allCards.filter(c => c.owner === activePlayer && c.status === 'HAND');
      const hasColor = myHand.some(c => c.color === leadColor);
      if (hasColor) return card.color === leadColor;
      return true;
  };

  const DIST_NORD = 260; const DIST_SUD  = 260; const DIST_OUEST= 380; const DIST_EST  = 380;
  const LABEL_OFFSET = 100; const HAND_RADIUS = 600; const ANGLE_STEP = 5;

  const getCardStyle = (card: CardData): React.CSSProperties => {
      if (card.status === 'WON') {
          const winPositions = [`translate(0px, 600px)`, `translate(-800px, 0px)`, `translate(0px, -600px)`, `translate(800px, 0px)`];
          return { 
              transform: `translate(-50%, -50%) ${winPositions[card.winnerIndex || 0]} scale(0.4)`, 
              opacity: 0, 
              zIndex: 500, 
              transition: 'transform 0.8s ease-in-out, opacity 0.8s ease-in-out'
          };
      }

      if (card.status === 'TABLE') {
          const rX = (card.tableRotation * 5) % 15 - 7;
          const rY = (card.tableRotation * 7) % 15 - 7;
          return { 
              transform: `translate(-50%, -50%) translate(${rX}px, ${rY}px) rotate(${card.tableRotation}deg) scale(1.1)`, 
              opacity: 1, 
              zIndex: card.zIndex, 
              transition: 'transform 0.5s ease-in-out, opacity 0.5s ease-in-out'
          };
      }
      
      const cardsInHand = allCards.filter(c => c.owner === card.owner && c.status === 'HAND');
      const colorPriority: Record<string, number> = { 'Blue': 1, 'Green': 2, 'Pink': 3, 'Yellow': 4, 'Rocket': 5 };
      cardsInHand.sort((a, b) => { if (a.color !== b.color) return colorPriority[a.color] - colorPriority[b.color]; return a.value - b.value; });
      
      const myIndex = cardsInHand.findIndex(c => c.id === card.id);
      const total = cardsInHand.length;
      const angle = (myIndex - (total - 1) / 2) * ANGLE_STEP;
      const centerHTML = "translate(-50%, -50%)";
      
      let playerRotation = 0; let distance = 0;
      switch(card.owner) {
        case 0: playerRotation = 0; distance = DIST_SUD; break;
        case 1: playerRotation = 90; distance = DIST_OUEST; break;
        case 2: playerRotation = 180; distance = DIST_NORD; break;
        case 3: playerRotation = -90; distance = DIST_EST; break;
      }

      const transform = `${centerHTML} rotate(${playerRotation}deg) translate(0px, ${distance + HAND_RADIUS}px) rotate(${angle}deg) translate(0px, -${HAND_RADIUS}px)`;

      return { 
          transform, 
          opacity: 1, 
          zIndex: myIndex,
          transition: 'transform 0.5s ease-out'
      };
  };

  const getMissionContainerStyle = (playerIndex: number): React.CSSProperties => {
      const base = "translate(-50%, -50%)";
      switch(playerIndex) {
          case 0: return { transform: `${base} translate(0px, ${DIST_SUD - LABEL_OFFSET}px)` }; 
          case 1: return { transform: `${base} translate(-${DIST_OUEST - LABEL_OFFSET}px, 0px) rotate(90deg)` }; 
          case 2: return { transform: `${base} translate(0px, -${DIST_NORD - LABEL_OFFSET}px) rotate(180deg)` }; 
          case 3: return { transform: `${base} translate(${DIST_EST - LABEL_OFFSET}px, 0px) rotate(-90deg)` }; 
          default: return {};
      }
  };

  return (
    <div className="w-full h-full relative bg-[#1e293b] overflow-hidden shadow-inner flex items-center justify-center select-none">
        <div className="relative w-0 h-0 flex items-center justify-center">

            {/* ZONES DES MISSIONS */}
            {[0, 1, 2, 3].map(pid => {
                const pMissions = missions.filter(m => m.ownerIndex === pid);
                if (pMissions.length === 0) return null;
                return (
                    <div key={pid} className="absolute flex gap-2 pointer-events-none" style={getMissionContainerStyle(pid)}>
                        {pMissions.map((m, i) => <MissionToken key={i} mission={m} />)}
                    </div>
                );
            })}

            {/* CARTES */}
            {allCards.map((card) => {
                const style = getCardStyle(card);
                const probKey = `${card.color}-${card.value}`;
                const prob = probabilities[probKey];
                const isSuggested = suggestedCardId === probKey;
                const isHand = card.status === 'HAND';
                // On calcule si c'est jouable pour le style (gris√© ou non)
                const isPlayable = isHand && getPlayableStatus(card);

                return (
                    <div 
                        key={card.id}
                        // Modification ici : Suppression de cursor-pointer et hover:-translate
                        className={`absolute top-0 left-0 w-20 aspect-[2/3]
                            ${isHand && !isPlayable ? 'grayscale opacity-60 brightness-75' : ''} 
                        `}
                        style={{ 
                            ...style, 
                            willChange: 'transform, opacity',
                            boxShadow: isSuggested ? '0 0 30px 10px rgba(59, 130, 246, 0.7)' : undefined
                        }}
                        // Modification ici : Suppression de onClick
                    >
                        {isSuggested && <div className="absolute -inset-1 bg-blue-500 rounded-lg animate-pulse blur-sm -z-10"></div>}

                        <Card color={card.color} value={card.value} className="w-full h-full shadow-2xl border border-black/30 rounded-lg" />
                        
                        {/* STATS IA */}
                        {prob !== undefined && isPlayable && (
                            <div className="absolute top-8 left-0 right-0 flex justify-center z-50">
                                <div className="bg-black/70 backdrop-blur-[2px] rounded px-1.5 py-0.5 shadow-sm border border-white/10 flex flex-col items-center">
                                    <span className={`text-sm font-black leading-none ${
                                        prob >= 80 ? 'text-green-400' : 
                                        prob <= 20 ? 'text-red-400' : 'text-yellow-400'
                                    }`}>
                                        {prob}%
                                    </span>
                                </div>
                            </div>
                        )}
                    </div>
                );
            })}
        </div>
    </div>
  );
}